<template>
  <label
    class="tu-radio"
    :class="[{ 'is-checked': model === label }]"
    role="radio"
    :aria-checked="model === label"
  >
    <span
      class="tu-radio__input"
      :class="{
        'is-checked': model === label,
      }"
    >
      <span class="tu-radio__inner"></span>
      <input
        ref="radio"
        class="tu-radio__original"
        type="radio"
        aria-hidden="true"
        tabindex="-1"
        autocomplete="off"
        v-model="model"
        :value="label"
        :name="name"
        @change="handleChange"
      />
    </span>
    <span class="tu-radio__label" @keydown.stop>
      <slot></slot>
      <template v-if="!$slots.default">{{ label }}</template>
    </span>
  </label>
</template>

<script>
export default {
  name: "tu-radio",

  props: {
    name: String,
    value: {},
    label: {},
  },

  computed: {
    //radio数据模型
    model: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit("input", val);
        this.$refs.radio &&
          (this.$refs.radio.checked = this.model === this.label);
      },
    },
  },

  methods: {
    handleChange() {
      this.$nextTick(() => {
        this.$emit("change", this.model);
      });
    },
  },
};
</script>
